import { Component, OnInit, OnDestroy } from '@angular/core';

import { PlayerService } from "../../player/player.service"

@Component({
  selector: 'app-player-avatar',
  templateUrl: './player-avatar.component.html',
  styleUrls: ['./player-avatar.component.css']
})
export class PlayerAvatarComponent implements OnInit, OnDestroy {
  playerClass: string = "barbarian";
  playerHP: number;
  constructor(private playerService: PlayerService) {
    this.playerService.playerHpSubject.subscribe(data => this.playerHP = data)
   }

  ngOnInit(): void {
    this.playerHP = this.playerService.getLifePoints()
  }

  ngOnDestroy(): void {
    this.playerService.playerHpSubject.unsubscribe()
  }
}
