import { Timing, Targets, RawEffect } from "../data/cards"
import { Card } from "./card/card.model"

export class Effect {
  public timing: Timing | Timing[]
  public target: Targets | Targets[]
  public type: "draw" | "summon" | "damage" | "healing" | "weaken" | "strengthen" | "ramp"
  public amount: number
  public card: Card | null
  constructor(effect: RawEffect) {
    this.timing = effect.timing
    this.target = effect.target
    this.type = effect.type
    this.amount = effect.amount ? effect.amount : 0
    this.card = effect.card ? effect.card : null
  }
}
