import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";

import { Card } from '../shared/card/card.model'

interface GameData {
  melee: Card | ""
  ranged: Card | ""
  mental: Card | ""
  turn: number
  hand: number
  deck: number
}

@Injectable({providedIn: 'root'})

export class HttpService {

  constructor(private http: HttpClient) {}

  sendData(gameData: GameData, player: number) {
    return this.http.put<GameData>("https://recipe-book-app-bce43.firebaseio.com/recipes/player" + player + ".json", gameData)
  }

  getData(player: number) {
    return this.http.get<GameData>("https://recipe-book-app-bce43.firebaseio.com/recipes/player" + player + ".json")
  }

}
