import { Component, OnInit, OnDestroy } from '@angular/core'

import { HandService } from "./hand.service";

import { Card } from "../../shared/card/card.model"

@Component({
  selector: 'app-hand',
  templateUrl: './hand.component.html',
  styleUrls: ['./hand.component.css']
})

export class HandComponent implements OnInit, OnDestroy {
  cards: Card[] = []

  constructor(private handService: HandService) {
    this.handService.handSubject.subscribe(data => this.cards = data)
   }

  ngOnInit(): void {
    this.handService.drawCards(4)
  }

  ngOnDestroy(): void {
    this.handService.handSubject.unsubscribe()
  }



  // calculateRotation(index: number) {
  //   let steps = 90 / this.cards.length
  //   return {transform: `rotate(${-45 + steps * index}deg)`, position: "static"}
  // }
}
