import { Injectable } from '@angular/core';
import { Card } from '../shared/card/card.model';

import { BattlefieldService } from '../battlefield/battlefield.service';
import { PlayerService } from '../player/player.service';
import { OpponentService } from '../opponent/opponent.service';
import { TurnService } from './turn.service';

@Injectable({ providedIn: 'root' })
export class CombatService {
  constructor(
    private battlefieldService: BattlefieldService,
    private playerService: PlayerService,
    private turnService: TurnService,
    private opponentService: OpponentService
  ) {}

  commenceCombat() {
    const rows = ['mental', 'ranged', 'melee'];
    //efekty na pocztku walki albo na reveal
    rows.forEach((row) => {
      let playerCard: Card = this.battlefieldService.getCard(row);
      let oppCard: Card = this.battlefieldService.getCard(row + 'Opp');
      this.revealAndCombatAbilities(playerCard, oppCard, row)
      //po efektach sprawdzam sba
      this.battlefieldService.buryTheFallen();
    });
    //waciwe ataki
    rows.forEach((row) => {
      let playerCard: Card = this.battlefieldService.getCard(row);
      let oppCard: Card = this.battlefieldService.getCard(row + 'Opp');
      //waciwe ataki
      this.attacks(playerCard, oppCard, row)
      this.battlefieldService.buryTheFallen();
    });
    //sprawdzamy czy s efekty eot i je aplikujemy:
    rows.forEach((row) => {
      let playerCard: Card = this.battlefieldService.getCard(row);
      let oppCard: Card = this.battlefieldService.getCard(row + 'Opp');
      this.eotAbilities(playerCard, oppCard, row)
      //po efektach sprawdzam sba
      this.battlefieldService.buryTheFallen();
    });
    //ustawiamy status wszystkiego co przeyo jako revealed
    rows.forEach((row) => {
      let playerCard: Card = this.battlefieldService.getCard(row);
      let oppCard: Card = this.battlefieldService.getCard(row + 'Opp');
      if (playerCard) {
        this.battlefieldService.markAsRevealed(row);
      } else if (oppCard) {
        this.battlefieldService.markAsRevealed(row + 'Opp');
      }
    });
    this.turnService.nextTurn();
  }
  clash(playerCard: Card, oppCard: Card, row: string) {
    if (playerCard.speed === oppCard.speed) {
      //jeli karty maj tak sam szybko, to bij w siebie nawzajem, a dopiero potem sprawdzamy czy kto zgin
      this.friendlyAttackAbilities(playerCard, row)
      this.oppAttackAbilities(oppCard, row)
      playerCard.abilities.includes("trample") && playerCard.attack > oppCard.hp ? this.opponentService.changeOppHp(-(playerCard.attack - oppCard.hp)) : null;
      this.battlefieldService.changeCardHp(-playerCard.attack, row + 'Opp');
      oppCard.abilities.includes("trample") && oppCard.attack > playerCard.hp ? this.playerService.changeLifePoints(-(oppCard.attack - playerCard.hp)) : null;
      this.battlefieldService.changeCardHp(-oppCard.attack, row);
      this.battlefieldService.buryTheFallen();
    } else if (playerCard.speed < oppCard.speed) {
      //jeli karty maj rn szybko, to najpierw szybsza zadaje obraenia, a wolniejsza tylko jeli przeyje
      // tutaj karta gracza jest wolniejsza, wic przeciwnik pierwszy zadaje obraenia
      this.oppAttackAbilities(oppCard, row)
      oppCard.abilities.includes("trample") && oppCard.attack > playerCard.hp ? this.playerService.changeLifePoints(-(oppCard.attack - playerCard.hp)) : null;
      this.battlefieldService.changeCardHp(-oppCard.attack, row);
      this.battlefieldService.buryTheFallen();
      //sprawdzamy czy karta gracza nadal yje
      if (this.battlefieldService.getCard(row) === playerCard) {
        //jak tak to te zadaje obraeni
        this.friendlyAttackAbilities(playerCard, row)
        playerCard.abilities.includes("trample") && playerCard.attack > oppCard.hp ? this.opponentService.changeOppHp(-(playerCard.attack - oppCard.hp)) : null;
        this.battlefieldService.changeCardHp(-playerCard.attack, row + 'Opp');
        this.battlefieldService.buryTheFallen();
      }
    } else {
      // tutaj karta przeciwnika jest wolniejsza, wic gracz pierwszy zadaje obraenia
      this.friendlyAttackAbilities(playerCard, row)
      playerCard.abilities.includes("trample") && playerCard.attack > oppCard.hp ? this.opponentService.changeOppHp(-(playerCard.attack - oppCard.hp)) : null;
      this.battlefieldService.changeCardHp(-playerCard.attack, row + 'Opp');
      this.battlefieldService.buryTheFallen();
      //sprawdzamy czy karta przeciwnika nadal yje
      if (this.battlefieldService.getCard(row + 'Opp') === oppCard) {
        //jak tak to te zadaje obraeni
        this.oppAttackAbilities(oppCard, row)
        oppCard.abilities.includes("trample") && oppCard.attack > playerCard.hp ? this.playerService.changeLifePoints(-(oppCard.attack - playerCard.hp)) : null;
        this.battlefieldService.changeCardHp(-oppCard.attack, row);
        this.battlefieldService.buryTheFallen();
      }
    }
  }
  revealAndCombatAbilities(playerCard: Card, oppCard: Card, row: string): void {
    let abilityTypes = ["silence", "shield", "damage", "weaken", "healing", "strengthen", "summon", "bounce", "ramp"]
    //sprawdzamy czy sa efekty negatywne na poczatku walki
    if (
      playerCard &&
      playerCard.effect &&
      ((playerCard.effect.timing === 'reveal' && !playerCard.revealed) ||
        playerCard.effect.timing === 'combat') && (playerCard.effect.type === "damage" || playerCard.effect.type === "weaken")
    ) {
      console.log(
        `${playerCard.name} uywa negatywnej zdolnoci na pocztku walki ${playerCard.effect.type} ${playerCard.effect.target} o wartoci ${playerCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(playerCard, row);
    } else if (
      oppCard &&
      oppCard.effect &&
      ((oppCard.effect.timing === 'reveal' && !oppCard.revealed) ||
        oppCard.effect.timing === 'combat') && (oppCard.effect.type === "damage" || oppCard.effect.type === "weaken")
    ) {
      console.log(
        `${oppCard.name} uywa negatywnej zdolnoci na pocztku walki ${oppCard.effect.type} na ${oppCard.effect.target} o wartoci ${oppCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(oppCard, row + 'Opp');
    }
    //sprawdzamy czy sa efekty pozytywne na poczatku walki
    if (
      playerCard &&
      playerCard.effect &&
      ((playerCard.effect.timing === 'reveal' && !playerCard.revealed) ||
        playerCard.effect.timing === 'combat') && playerCard.effect.type !== "damage" && playerCard.effect.type !== "weaken"
    ) {
      console.log(
        `${playerCard.name} uywa pozytywnej zdolnoci na pocztku walki ${playerCard.effect.type} na ${playerCard.effect.target} o wartoci ${playerCard.effect.card ? playerCard.effect.card.name : playerCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(playerCard, row);
    } else if (
      oppCard &&
      oppCard.effect &&
      ((oppCard.effect.timing === 'reveal' && !oppCard.revealed) ||
        oppCard.effect.timing === 'combat') && oppCard.effect.type !== "damage" && oppCard.effect.type !== "weaken"
    ) {
      console.log(
        `${oppCard.name} uywa pozytywnej zdolnoci na pocztku walki ${oppCard.effect.type} na ${oppCard.effect.target} o wartoci ${oppCard.effect.card ? oppCard.effect.card.name : oppCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(oppCard, row + 'Opp');
    }
  }
  attacks(playerCard: Card, oppCard: Card, row: string) {
    if (playerCard) {
      if (oppCard) {
        this.clash(playerCard, oppCard, row);
      } else {
        //sprawdzamy czy s efekty podczas ataku i je aplikujemy
        this.friendlyAttackAbilities(playerCard, row)
        console.log(
          `${playerCard.name} atakuje przeciwnika za ${playerCard.attack} obrae.`
        );
        this.opponentService.changeOppHp(-playerCard.attack);
      }
    } else if (oppCard) {
      //sprawdzamy czy s efekty podczas ataku i je aplikujemy
      this.oppAttackAbilities(oppCard, row)
      this.playerService.changeLifePoints(-oppCard.attack);
      console.log(
        `${oppCard.name} atakuje przeciwnika za ${oppCard.attack} obrae.`
      );
    }
  }
  friendlyAttackAbilities(playerCard: Card, row: string): void {
    if (
      playerCard &&
      playerCard.effect &&
      playerCard.effect.timing === 'attack'
    ) {
      console.log(
        `${playerCard.name} uywa zdolnoci na ataku ${playerCard.effect.type} na ${playerCard.effect.target} warto ${playerCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(playerCard, row);
    }
  }
  oppAttackAbilities(oppCard: Card, row: string): void {
 if (
      oppCard &&
      oppCard.effect &&
      oppCard.effect.timing === 'attack'
    ) {
      console.log(
        `${oppCard.name} uywa zdolnoci na ataku ${oppCard.effect.type} na ${oppCard.effect.target} warto ${oppCard.effect.amount}`
      );
      this.battlefieldService.applyBattlefiedEffect(oppCard, row + 'Opp');
    }
  }
  eotAbilities(playerCard: Card, oppCard: Card, row: string): void {
    if (
      playerCard &&
      playerCard.effect &&
      playerCard.effect.timing === 'eot'
    ) {
      this.battlefieldService.applyBattlefiedEffect(playerCard, row);
      console.log(
        `${playerCard.name} uywa zdolnoci EOT ${playerCard.effect.type} na ${playerCard.effect.target}`
      );
    } else if (oppCard && oppCard.effect && oppCard.effect.timing === 'eot') {
      this.battlefieldService.applyBattlefiedEffect(oppCard, row + 'Opp');
      console.log(
        `${oppCard.name} uywa zdolnoci EOT ${oppCard.effect.type} na ${oppCard.effect.target}`
      );
    }
  }
}
