import { Injectable } from '@angular/core';
import { Subject } from 'rxjs'

@Injectable({providedIn: 'root'})

export class OpponentService {
  private oppDeckSize: number = 36
  private oppHandSize: number = 4
  private oppHp: number = 30


  oppHpEmitter = new Subject<number>()
  oppHandSizeEmitter = new Subject<number>()
  oppDeckSizeEmitter = new Subject<number>()

  constructor() {}

  getOppHp() {
    return this.oppHp
  }
  changeOppHp(number: number) {
    console.log(`ycie przeciwnika zmienia si z ${this.oppHp} na ${this.oppHp + number}`);
    this.oppHp += number
    this.oppHpEmitter.next(this.oppHp)
  }
  getOppHandSize() {
    return this.oppHandSize
  }
  setOppHandSize(number: number) {
    this.oppHandSize = number
    this.oppHandSizeEmitter.next(number)
  }
  getOppDeckSize() {
    return this.oppDeckSize
  }
  setOppDeckSize(number: number) {
    this.oppDeckSize = number
    this.oppDeckSizeEmitter.next(number)
  }
}
