import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-opponent-hand',
  templateUrl: './opponent-hand.component.html',
  styleUrls: ['./opponent-hand.component.css']
})
export class OpponentHandComponent implements OnInit {
  @Input() oppHandSize: number[]

  constructor() { }

  ngOnInit(): void {
  }

  calculateRotation(index: number) {
    let steps = 90 / this.oppHandSize.length
    return {transform: `rotate(${45 - steps * index}deg)`}
  }

}
