import { Injectable } from '@angular/core';
import { Subject } from 'rxjs'

import { Card } from '../../shared/card/card.model';
import { DeckService } from '../deck/deck.service'

@Injectable({providedIn: 'root'})

export class HandService {
  private hand: Card[] = []
  private maxHandSize: number = 8

  public handSubject = new Subject<Card[]>()

  constructor(private deckService: DeckService) {}

  getHand() {
    return this.hand;
  }
  setHand(hand: Card[]) {
    this.hand = hand
    this.handSubject.next(this.hand)
  }
  drawCards(number: number) {
    for (let index = 0; index < number; index++) {
      console.log("Gracz dobiera kart.");
      this.hand.length < this.maxHandSize ? this.hand = this.hand.concat(this.deckService.draw(1)) : this.deckService.mill(1)
    }
    this.handSubject.next(this.hand)
  }
  removeCard(index: number) {
    this.hand.splice(index, 1)
    this.handSubject.next(this.hand)
  }
  getHandSize() {
    return this.hand.length
  }
  setMaxHandSize(number: number) {
    this.maxHandSize = number
  }
}
