import { Injectable } from '@angular/core';
import { Card } from '../shared/card/card.model';
// import { Effect } from '../shared/effect.model';
// import { Subject } from 'rxjs'
// import { cards } from '../data/cards'

// import { DeckService } from "../player/deck/deck.service"
import { HandService } from '../player/hand/hand.service';
import { PlayerService } from '../player/player.service';
import { OpponentService } from '../opponent/opponent.service';
import { ActionPointsService } from '../player/action-points/action-points.service';

// import { Card } from "../shared/card/card.model"

@Injectable({ providedIn: 'root' })
export class EffectsService {
  constructor(
    private handService: HandService,
    private playerService: PlayerService,
    private opponentService: OpponentService,
    private actionPointsService: ActionPointsService
  ) {}

  applyEffect(card: Card, target: string = '') {
    switch (card.effect.type) {
      case 'draw':
        console.log(`${card.name} sprawia, e ${card.effect.target} dobiera ${card.effect.amount} kart`);
        this.handService.drawCards(card.effect.amount);
        break;
      case 'ramp':
        console.log(`${card.name} sprawia, e ${card.effect.target} dostaje ${card.effect.amount} PA`);
        this.actionPointsService.addActionPoints(card.effect.amount);
        break;
      case 'healing':
        console.log(`${card.name} sprawia, e ${target} dostaje ${card.effect.amount === -1 ? card.attack : card.effect.amount} ycia`);
        target === 'player'
          ? this.playerService.changeLifePoints(
              card.effect.amount === -1 ? card.attack : card.effect.amount
            )
          : this.opponentService.changeOppHp(
              card.effect.amount === -1 ? card.attack : card.effect.amount
            );
        break;
      case 'damage':
        console.log(`${card.name} sprawia, e ${target} dostaje ${card.effect.amount === -1 ? -card.attack : -card.effect.amount} obrae`);
        target === 'player'
          ? this.playerService.changeLifePoints(
              card.effect.amount === -1 ? -card.attack : -card.effect.amount
            )
          : this.opponentService.changeOppHp(
              card.effect.amount === -1 ? -card.attack : -card.effect.amount
            );
        break;
      default:
        break;
    }
  }
}
