import { Component, OnInit, OnDestroy } from '@angular/core';
import { CdkDragDrop } from '@angular/cdk/drag-drop'
import { Card } from '../shared/card/card.model';

import { BattlefieldService } from './battlefield.service'
import { HandService } from '../player/hand/hand.service'
import { ActionPointsService } from '../player/action-points/action-points.service'
import { OpponentService} from '../opponent/opponent.service'

@Component({
  selector: 'app-battlefield',
  templateUrl: './battlefield.component.html',
  styleUrls: ['./battlefield.component.css']
})
export class BattlefieldComponent implements OnInit, OnDestroy {
  melee: Card | null = null
  ranged: Card | null = null
  mental: Card | null = null
  meleeOpp: Card | null = null
  rangedOpp: Card | null = null
  mentalOpp: Card | null = null
  showMental: boolean = true
  showRanged: boolean = true
  showMelee: boolean = true

  oppHP: number

  constructor(private battlefieldService: BattlefieldService, private handService: HandService, private actionsPointsService: ActionPointsService, private opponentService: OpponentService) {
    this.battlefieldService.cardEmitter.subscribe((arg: {card: Card | null, row: string}) => this[arg.row] = arg.card)
    this.opponentService.oppHpEmitter.subscribe((arg: number) => this.oppHP = arg)
   }

  ngOnInit(): void {
    this.oppHP = this.opponentService.getOppHp()
    this.rangedOpp = this.battlefieldService.getCard("rangedOpp")
  }

  drop(event: CdkDragDrop<string>) {
    this.showMental = true;
    this.showRanged = true
    this.showMelee = true
    if ((event.container.data === "melee" || event.container.data === "ranged" || event.container.data === "mental") && event.item.data.card.cost <= this.actionsPointsService.getActionPoints().current) {
      this.actionsPointsService.payCosts(event.item.data.card.cost)
      this.battlefieldService.setCard(event.item.data.card, event.container.data)
      this.handService.removeCard(event.item.data.index)
      if (event.item.data.card.effect && event.item.data.card.effect.timing === "play") {
        this.battlefieldService.applyBattlefiedEffect(event.item.data.card, event.container.data)
        console.log(
          `Gracz wstawia ${event.item.data.card.name} w ${event.container.data} z efektem ${event.item.data.card.effect.type} na ${event.item.data.card.effect.target}`
        );
      } else {
        console.log(`Gracz wstawia ${event.item.data.card.name} w ${event.container.data}`);
      }
    }
  }

  ngOnDestroy(): void {
    this.battlefieldService.cardEmitter.unsubscribe()
    this.opponentService.oppHpEmitter.unsubscribe()
  }

}
