import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { Card } from '../shared/card/card.model';
// import { Effect } from '../shared/effect.model'

import { DeckService } from '../player/deck/deck.service';
import { EffectsService } from '../shared/effects.service';
import { PlayerService } from '../player/player.service'
import { OpponentService } from '../opponent/opponent.service'

@Injectable({ providedIn: 'root' })
export class BattlefieldService {
  private melee: Card | null;
  private ranged: Card | null;
  private mental: Card | null;
  private meleeOpp: Card | null;
  private rangedOpp: Card | null;
  private mentalOpp: Card | null;

  cardEmitter = new Subject<{ card: Card | null; row: string }>();

  constructor(
    private deckService: DeckService,
    private effectsService: EffectsService,
    private playerService: PlayerService,
    private opponentService: OpponentService
  ) {}

  getCard(row: string) {
    return this[row];
  }
  setCard(card: Card, row: string) {
    this[row] ? this.deckService.discard(this[row]) : null;
    this[row] = card;
    this.cardEmitter.next({ card: card, row: row });
  }
  changeCardHp(number: number, row: string) {
    this[row].hp + number > this[row].maxHp
      ? (this[row].hp = this[row].maxHp)
      : (this[row].hp += number);
    number < 0
      ? console.log(`${this[row].name} w ${row} otrzymuje ${number} obrae`)
      : console.log(`${this[row].name} w ${row} jest leczony o ${number}`);
  }
  changeCardAttack(number: number, row: string) {
    this[row].attack + number < 0
      ? (this[row].attack = 0)
      : (this[row].attack += number);
    number < 0
      ? console.log(`${this[row].name} w ${row} otrzymuje ${number} ataku.`)
      : console.log(`${this[row].name} w ${row} traci ${number} ataku`);
  }
  removeCard(card: Card, row: string) {
    this.deckService.discard(card);
    console.log(this[row].name + ' umiera w ' + row);
    this[row] = null;
    this.cardEmitter.next({ card: null, row: row });
    if (card.effect && card.effect.timing === 'death') {
      this.applyBattlefiedEffect(card, row);
      console.log(`z efektem ${card.effect.type} na ${card.effect.target}`);
    }
  }
  buryTheFallen() {
    if (this.playerService.getLifePoints() < 0 && this.opponentService.getOppHp() < 0) {
      console.log("%cRemis", "color: red; font-size: 30px");
    } else if (this.playerService.getLifePoints() < 0) {
      console.log("%cGracz wygrywa", "color: red; font-size: 30px");
    } else {
      console.log("%cPrzeciwnik wygrywa", "color: red; font-size: 30px");
    }
    const rows = [
      'mental',
      'ranged',
      'melee',
      'mentalOpp',
      'rangedOpp',
      'meleeOpp',
    ];
    rows.forEach((row) => {
      if (this[row] && this[row].hp <= 0) {
        this.removeCard(this[row], row);
      }
    });
  }
  markAsRevealed(row: string) {
    this[row].revealed = true;
    console.log(this[row].name + ' ma status revealed');
  }
  calculateTargets(row: string, target: string | string[]): string[] {
    let targets: string[] = [];
    let playerRows = ['mental', 'ranged', 'melee'];
    let oppRows = ['mentalOpp', 'rangedOpp', 'meleeOpp'];
    if (row.includes('Opp')) {
      switch (target) {
        case 'player':
          targets.push('opponent');
          break;
        case 'opponent':
          targets.push('player');
          break;
        case 'self':
          targets.push(row);
          break;
        case 'playerAllies':
          targets = targets.concat(oppRows);
          break;
        case 'oppAllies':
          targets = targets.concat(playerRows);
          break;
        case 'sameRow':
          targets.push(row.substring(0, row.length - 3));
          break;
        case 'friendlyUpRow':
          oppRows.indexOf(row) - 1 >= 0
            ? targets.push(oppRows[oppRows.indexOf(row) - 1])
            : null;
          break;
        case 'friendlyDownRow':
          oppRows.indexOf(row) + 1 <= 2
            ? targets.push(oppRows[oppRows.indexOf(row) + 1])
            : null;
          break;
        case 'enemyUpRow':
          playerRows.indexOf(row.substring(0, row.length - 3)) - 1 >= 0
            ? targets.push(
                playerRows[
                  playerRows.indexOf(row.substring(0, row.length - 3)) - 1
                ]
              )
            : null;
          break;
        case 'enemyDownRow':
          playerRows.indexOf(row.substring(0, row.length - 3)) + 1 <= 2
            ? targets.push(
                playerRows[
                  playerRows.indexOf(row.substring(0, row.length - 3)) + 1
                ]
              )
            : null;
          break;
        case 'otherPlayerAllies':
          targets = row === "meleeOpp" ? ["rangedOpp", "mentalOpp"] : row === "rangedOpp" ? ["meleeOpp", "mentalOpp"] : ["meleeOpp", "rangedOpp"];
          break;
      }
    } else {
      switch (target) {
        case 'player':
          targets.push(target);
          break;
        case 'opponent':
          targets.push(target);
          break;
        case 'self':
          targets.push(row);
          break;
        case 'playerAllies':
          targets = targets.concat(playerRows);
          break;
        case 'oppAllies':
          targets = targets.concat(oppRows);
          break;
        case 'sameRow':
          targets.push(row + 'Opp');
          break;
        case 'friendlyUpRow':
          playerRows.indexOf(row) - 1 >= 0
            ? targets.push(playerRows[playerRows.indexOf(row) - 1])
            : null;
          break;
        case 'friendlyDownRow':
          playerRows.indexOf(row) + 1 <= 2
            ? targets.push(playerRows[playerRows.indexOf(row) + 1])
            : null;
          break;
        case 'enemyUpRow':
          oppRows.indexOf(row + 'Opp') - 1 >= 0
            ? targets.push(oppRows[oppRows.indexOf(row + 'Opp') - 1])
            : null;
          break;
        case 'enemyDownRow':
          oppRows.indexOf(row + 'Opp') + 1 <= 2
            ? targets.push(oppRows[oppRows.indexOf(row + 'Opp') + 1])
            : null;
          break;
        case 'otherPlayerAllies':
          targets = row === "melee" ? ["ranged", "mental"] : row === "ranged" ? ["melee", "mental"] : ["melee", "ranged"];
          break;
      }
    }
    return targets;
  }
  applyBattlefiedEffect(card: Card, row: string) {
    switch (card.effect.type) {
      case 'draw':
        row.includes('Opp') ? null : this.effectsService.applyEffect(card);
        break;
      case 'ramp':
        row.includes('Opp') ? null : this.effectsService.applyEffect(card);
        break;
      case 'healing':
        let healingTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                (allTargets = allTargets.concat(
                  this.calculateTargets(row, currentTarget)
                )),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolnoci to ${healingTargets}`);
        healingTargets.forEach((target) =>
          target === 'player' || target === 'opponent'
            ? this.effectsService.applyEffect(card, target)
            : this[target]
            ? this.changeCardHp(
                card.effect.amount === -1 ? card.attack : card.effect.amount,
                target
              )
            : null
        );
        break;
      case 'damage':
        let damageTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                (allTargets = allTargets.concat(
                  this.calculateTargets(row, currentTarget)
                )),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolnoci to ${damageTargets}`);
        damageTargets.forEach((target) =>
          target === 'player' || target === 'opponent'
            ? this.effectsService.applyEffect(card, target)
            : this[target]
            ? this.changeCardHp(
                card.effect.amount === -1 ? -card.attack : -card.effect.amount,
                target
              )
            : null
        );
        break;
      case 'weaken':
        let weakenTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                (allTargets = allTargets.concat(
                  this.calculateTargets(row, currentTarget)
                )),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolnoci to ${weakenTargets}`);
        weakenTargets.forEach((target) =>
          this[target]
            ? this.changeCardAttack(-card.effect.amount, target)
            : null
        );
        break;
      case 'strengthen':
        let strengthenTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                (allTargets = allTargets.concat(
                  this.calculateTargets(row, currentTarget)
                )),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolnoci to ${strengthenTargets}`);
        strengthenTargets.forEach((target) =>
          this[target]
            ? this.changeCardAttack(card.effect.amount, target)
            : null
        );
        break;
      case 'summon':
        let summonTargets: string[] = Array.isArray(card.effect.target)
          ? card.effect.target.reduce(
              (allTargets, currentTarget) =>
                (allTargets = allTargets.concat(
                  this.calculateTargets(row, currentTarget)
                )),
              []
            )
          : this.calculateTargets(row, card.effect.target);
        console.log(`Cele zdolnoci to ${summonTargets}`);
        summonTargets.forEach((target) =>
          !this[target]
            ? this.setCard(new Card(card.effect.card), target)
            : null
        );
        break;
      default:
        break;
    }
  }
}
