import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { DeckService } from "./deck/deck.service"

import { Card } from '../shared/card/card.model';

import { aggro, control } from "../data/cards"

@Injectable({providedIn: 'root'})

export class PlayerService {
  private hp: number = 30
  private playerNumber = 1

  playerHpSubject = new Subject<number>()
  playerNumberSubject = new Subject<number>()

  constructor(private deckService: DeckService) {}

  getLifePoints() {
    return this.hp
  }
  changeLifePoints(number: number) {
    console.log(`ycie gracza zmienia si z ${this.hp} na ${this.hp + number}`);
    this.hp += number
    this.playerHpSubject.next(this.hp)
  }
  getPlayerNumber() {
    return this.playerNumber
  }
  setPlayerNumber(number: number) {
    this.playerNumber = number
    this.playerNumber === 1 ? this.deckService.setDeck(aggro.map(card => new Card(card))) : this.deckService.setDeck(control.map(card => new Card(card)))
    this.playerNumberSubject.next(this.playerNumber)
  }
}
