import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

import { ActionPointsService } from '../player/action-points/action-points.service';
import { HandService } from '../player/hand/hand.service'

@Injectable({providedIn: 'root'})

export class TurnService {
  private turn: number = 1

  turnSubject = new Subject<number>()

  constructor(
    private actionsPointsService: ActionPointsService,
    private handService: HandService
  ) {}

  getTurn() {
    return this.turn
  }
  nextTurn() {
    this.turn += 1
    console.log(`Nastpuje tura ${this.turn}`);
    this.actionsPointsService.addActionPoints(1);
    this.actionsPointsService.refreshActionPoints();
    this.handService.drawCards(1);
    this.turnSubject.next(this.turn)
  }
}
